import React, { Component } from "react";
import cronista from "./cronista.png";
import ambito from "./ambito.png";
import "./App.css";

export default class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      ambito: []
    };
  }

  async componentDidMount() {
    let response = await fetch("https://warm-tor-82746.herokuapp.com/economics")
      .then(res => res.json())
      .then(data => data);

    this.setState(
      Object.assign({}, this.state, {
        ambito: response
      })
    );
  }

  render() {
    return (
      <div className="App">
        <header className="App-header">
          <h1>Noticias del dia ðŸ—ž</h1>
          {this.state.ambito.map((notice, i) => {
            if (notice.title || notice.url) {
              return (
                <div className="new" key={i}>
                  <div className="new-photo">
                    <a href={notice.url}>
                      {notice.img === "cronista" ? (
                        <img src={cronista} alt={notice.title} />
                      ) : (
                        <img src={ambito} alt={notice.title} />
                      )}
                    </a>
                  </div>
                  <div className="new-content">
                    <h1>
                      <a href={notice.url}>{notice.title}</a>
                    </h1>
                  </div>
                </div>
              );
            }
            return null;
          })}
        </header>
      </div>
    );
  }
}
